@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap);
*{
  padding: 0;
  margin: 0;
  outline: 0;
}
:root{
  --backGroundColour: #1F2328;
  --fontColour: #ffffff;
  --mons: 'Montserrat', sans-serif;
  --oSans: 'Open Sans', sans-serif;
}

body{
  background-color: #1F2328;
  background-color: var(--backGroundColour);
  padding: 10px;
}
.body{
  width: 100%;
  display: flex;
  justify-content: center;
}

.body h1{
  color: var(--fontColour);
  font-family: var(--mons);
  align-self: center;
  font-size: 50px;
}
.socials{
  text-align: center;
  padding: 10px;
}

#ghub{
  padding: 10px;
}
.github{
  color: #ffff;
  font-size: 30px;
  -webkit-transition: color 200ms cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: color 200ms cubic-bezier(0.075, 0.82, 0.165, 1);
}
.github:hover{
  color:#FD1016;
  opacity: 0.8;
}
.description{
  text-align: center;
  font-family: var(--oSans);
  font-size: 20px;
  font-weight: 600;
  color: #ffff;
  padding: 10px;
}

.describo{
  background-color: #FD1016;
  padding: 5px;
  color: #ffff;
}
.wrapper{
  width: 100%;
  position: relative;
  top: 300px;
}
